<template>
  <v-app>
    <v-app-bar
      app
      color="white"
      class="border-bottom"
      flat
      v-if="$store.getters.isLoggedIn"
    >
      <v-list class="pa-0" color="transparent">
        <v-list-item>
          <v-list-item-avatar>
            <v-img src="https://www.gravatar.com/avatar/blank"></v-img>
          </v-list-item-avatar>

          <v-list-item-title>{{
            $store.getters.getUsername
          }}</v-list-item-title>
        </v-list-item>
      </v-list>
      <v-spacer></v-spacer>
      <v-btn color="primary" @click="logout">
        <v-icon class="ml-4">logout</v-icon>
        <span>تسجيل الخروج</span>
      </v-btn>
    </v-app-bar>
    <v-navigation-drawer
      app
      disable-resize-watcher
      :mini-variant="variant"
      permanent
      right
      v-if="$store.getters.isLoggedIn"
    >
      <template v-slot:prepend>
        <v-btn
          block
          color="primary"
          large
          depressed
          tile
          @click="variant = !variant"
        >
          <v-icon class="ml-4">menu</v-icon>
          <span>القائمة</span>
        </v-btn>
      </template>

      <v-list>
        <v-list-item
          v-for="list in routeList"
          :key="list.title"
          link
          :to="list.path"
          color="primary"
        >
          <v-list-item-icon>
            <v-icon>{{ list.icon }}</v-icon>
          </v-list-item-icon>

          <v-list-item-content>
            <v-list-item-title>{{ list.text }}</v-list-item-title>
          </v-list-item-content>
        </v-list-item>
      </v-list>
    </v-navigation-drawer>
    <v-main>
      <router-view />
    </v-main>
  </v-app>
</template>

<script>
export default {
  name: "app",
  data() {
    return {
      variant: true,
      routeList: [
        {
          text: "الرئيسية",
          icon: "home",
          path: "/",
        },
        {
          text: "الطلبات",
          icon: "view_list",
          path: "/orders",
        },
        {
          text: "المنتجات",
          icon: "category",
          path: "/products",
        },
      ],
    };
  },
  methods: {
    logout() {
      let confirm = window.confirm("تسجيل الخروج ؟");
      if (confirm) {
        this.$store
          .dispatch("logout")
          .then(() => {
            this.axios.defaults.headers.common["Authorization"] = "";
          })
          .catch((err) => {
            console.error(err);
          });
      } else {
        return;
      }
    },
  },
};
</script>

<style lang="scss">
.border-bottom .v-toolbar__content {
  border-bottom: 1px solid rgba(0, 0, 0, 0.12) !important;
  border-color: rgba(0, 0, 0, 0.12) !important;
  border-width: 1px !important;
}
</style>