<template>
  <div id="OrdersPage" class="pa-4">
    <v-card>
      <v-toolbar flat color="primary">
        <span class="secondary--text">الطلبات</span>
        <v-spacer></v-spacer>
        <v-btn color="secondary" depressed dark @click="newOrderDialog = true">
          <span class="primary--text">اضافة طلب جديد</span>
        </v-btn>
      </v-toolbar>
      <v-dialog v-model="newOrderDialog" max-width="650px">
        <v-card>
          <v-toolbar flat>
            <span>اضافة الطلب</span>
            <v-spacer></v-spacer>
            <v-btn icon @click="newOrderDialog = false">
              <v-icon>close</v-icon>
            </v-btn>
          </v-toolbar>
          <v-divider></v-divider>
          <v-form
            ref="newOrder"
            v-model="validNewForm"
            class="pa-4"
            @submit.prevent="createOrder"
          >
            <v-text-field
              v-model="orderToSave.username"
              label="اسم الزبون"
              outlined
              :rules="newOrderFormRules"
            ></v-text-field>
            <v-text-field
              v-model="orderToSave.phone"
              label="رقم الهاتف"
              outlined
              :rules="newOrderFormRules"
            ></v-text-field>
            <v-text-field
              v-model="orderToSave.address"
              label="العنوان"
              outlined
              :rules="newOrderFormRules"
            ></v-text-field>
            <v-text-field
              v-model="orderToSave.product_name"
              label="اسم المنتج"
              outlined
              :rules="newOrderFormRules"
            ></v-text-field>
            <v-text-field
              v-model="orderToSave.product_quantity"
              label="الكمية"
              outlined
              :rules="newOrderFormRules"
            ></v-text-field>
            <v-select
              :items="orderToSaveStatusOptions"
              label="الحالة"
              item-text="text"
              item-value="value"
              outlined
              v-model="orderToSave.status"
            ></v-select>

            <v-btn block color="secondary" large type="submit">
              اضافة طلب جديد
            </v-btn>
          </v-form>
        </v-card>
      </v-dialog>
      <v-data-table :headers="headers" :items="Orders">
        <template v-slot:item.status="{ item }">
          <v-chip :color="getColor(item.status)" dark>
            {{ getStatus(item.status) }}
          </v-chip>
        </template>
        <template v-slot:item.changeStatus="{ item }">
          <v-select
            :items="statusOptions"
            label="تغير الحالة"
            item-text="text"
            item-value="statusId"
            outlined
            hide-details
            dense
            v-model="orderStatus"
            @change="setStatus($event, item.id)"
          ></v-select>
        </template>

        <template v-slot:item.actions="{ item }">
          <v-btn icon @click="openEditDialog(item.id, item)">
            <v-icon>edit</v-icon>
          </v-btn>
          <v-btn icon @click="deleteOrder(item.id, item)">
            <v-icon>delete</v-icon>
          </v-btn>
        </template>
      </v-data-table>
    </v-card>
    <v-dialog v-model="editDialog" max-width="650px">
      <v-card>
        <v-toolbar flat>
          <span>تعديل الطلب</span>
          <v-spacer></v-spacer>
          <v-btn icon @click="editDialog = false">
            <v-icon>close</v-icon>
          </v-btn>
        </v-toolbar>
        <v-divider></v-divider>
        <v-form
          ref="editOrder"
          v-model="validEditForm"
          class="pa-4"
          @submit.prevent="saveUpdate(orderToEditId)"
        >
          <v-text-field
            v-model="orderToEdit.username"
            label="اسم الزبون"
            outlined
          ></v-text-field>
          <v-text-field
            v-model="orderToEdit.phone"
            label="رقم الهاتف"
            outlined
          ></v-text-field>
          <v-text-field
            v-model="orderToEdit.address"
            label="العنوان"
            outlined
          ></v-text-field>
          <v-text-field
            v-model="orderToEdit.product_name"
            label="اسم المنتج"
            outlined
          ></v-text-field>
          <v-text-field
            v-model="orderToEdit.product_quantity"
            label="الكمية"
            outlined
          ></v-text-field>
          <v-select
            :items="orderToEditStatusOptions"
            label="الحالة"
            item-text="text"
            item-value="value"
            outlined
            v-model="orderToEdit.status"
          ></v-select>

          <v-btn block color="secondary" large type="submit">
            حفظ التعديلات
          </v-btn>
        </v-form>
      </v-card>
    </v-dialog>
  </div>
</template>

<script>
export default {
  data() {
    return {
      Orders: [],
      headers: [
        {
          text: "#",
          sortable: false,
          value: "id",
          align: "start",
        },
        {
          text: "اسم الزبون",
          sortable: false,
          value: "username",
          align: "start",
        },
        {
          text: "رقم الهاتف",
          sortable: false,
          value: "phone",
          align: "start",
        },
        {
          text: "العنوان",
          sortable: false,
          value: "address",
          align: "start",
        },
        {
          text: "المنتج",
          sortable: false,
          value: "product_name",
          align: "start",
        },
        {
          text: "الكمية",
          sortable: false,
          value: "product_quantity",
          align: "start",
        },
        {
          text: "الحالة",
          sortable: false,
          value: "status",
          align: "start",
        },
        {
          text: "تغير حالة الطلب",
          sortable: false,
          value: "changeStatus",
          align: "start",
        },
        {
          text: "اجرائات",
          sortable: false,
          value: "actions",
          align: "start",
        },
      ],
      statusOptions: [
        {
          text: "جديد",
          statusId: 0,
        },
        {
          text: "مرفوض",
          statusId: 1,
        },
        {
          text: "تم التسلم",
          statusId: 2,
        },
      ],
      orderStatus: "",
      editDialog: false,
      validEditForm: false,
      orderToEdit: {
        username: "",
        phone: "",
        address: "",
        product_name: "",
        product_quantity: "",
        status: "",
      },
      orderToEditId: "",
      orderToEditStatusOptions: [
        {
          text: "جديد",
          value: 0,
        },
        {
          text: "مرفوض",
          value: 1,
        },
        {
          text: "تم التسلم",
          value: 2,
        },
      ],
      validNewForm: false,
      orderToSave: {
        username: "",
        phone: "",
        address: "",
        product_name: "",
        product_quantity: "",
        status: "",
      },
      orderToSaveStatusOptions: [
        {
          text: "جديد",
          value: 0,
        },
        {
          text: "مرفوض",
          value: 1,
        },
        {
          text: "تم التسلم",
          value: 2,
        },
      ],
      newOrderDialog: false,
      newOrderFormRules: [(v) => !!v || "لا يمكن ترك هذا الحقل فارغ !"],
    };
  },
  mounted() {
    this.getOrders();
  },

  methods: {
    getOrders() {
      this.axios
        .get("/api/orders/read")
        .then((result) => {
          this.Orders = result.data.reverse();
        })
        .catch((err) => {
          console.error(err);
        });
    },
    getColor(status) {
      switch (status) {
        case 1:
          return "orange";
          break;
        case 2:
          return "green";
          break;
        default:
          return "red";
          break;
      }
    },

    getStatus(status) {
      switch (status) {
        case 1:
          return "مرفوض";
          break;
        case 2:
          return "تم التسليم";
          break;
        default:
          return "جديد";
          break;
      }
    },

    setStatus(e, id) {
      this.axios
        .put(`/api/orders/updateStatus/${id}`, { status: e })
        .then((result) => {
          this.getColor();
          this.getStatus();
          this.getOrders();
        })
        .catch((err) => {
          console.error(err);
        });
    },
    openEditDialog(id, item) {
      this.editDialog = true;
      this.orderToEdit.username = item.username;
      this.orderToEdit.phone = item.phone;
      this.orderToEdit.address = item.address;
      this.orderToEdit.product_name = item.product_name;
      this.orderToEdit.product_quantity = item.product_quantity;
      this.orderToEdit.status = item.status;
      this.orderToEditId = id;
    },
    createOrder() {
      if (this.$refs.newOrder.validate()) {
        this.axios
          .post("/api/orders/create", this.orderToSave)
          .then((result) => {
            this.getOrders();
            this.newOrderDialog = false;
          })
          .catch((err) => {
            console.error(err);
          });
      }
    },
    saveUpdate(id) {
      this.axios
        .put(`/api/orders/update/${id}`, this.orderToEdit)
        .then((result) => {
          this.getOrders();
          this.editDialog = false;
        })
        .catch((err) => {
          console.error(err);
        });
    },
    deleteOrder(id, item) {
      let index = this.Orders.indexOf(item);
      let confirm = window.confirm("حذف الطلب ؟");

      if (confirm) {
        this.axios
          .delete(`/api/orders/delete/${id}`)
          .then((result) => {
            this.Orders.splice(index, 1);
          })
          .catch((err) => {
            console.error(err);
          });
      }
    },
  },
};
</script>

<style>
</style>