import Vue from "vue";
import VueRouter from "vue-router";
import Home from "../views/Home.vue";
import Login from "../views/Login";
import Orders from "../views/Orders";
import Products from "../views/Products";
import store from "../store/";

Vue.use(VueRouter);

function GuardTheRoute(to, from, next) {
  let loggedIn = store.getters.isLoggedIn;
  if (loggedIn) {
    next();
  } else {
    next({ path: "/login" });
  }
}

function GuardTheLoginRoute(to, from, next) {
  let loggedIn = store.getters.isLoggedIn;
  if (loggedIn) {
    next({ path: "/" });
  } else {
    next();
  }
}

const routes = [
  {
    path: "/",
    name: "Home",
    component: Home,
    beforeEnter: GuardTheRoute,
  },
  {
    path: "/orders",
    name: "Orders",
    component: Orders,
    beforeEnter: GuardTheRoute,
  },
  {
    path: "/products",
    name: Products,
    component: Products,
    beforeEnter: GuardTheRoute,
  },
  {
    path: "/login",
    name: "Login",
    component: Login,
    beforeEnter: GuardTheLoginRoute,
  },
];

const router = new VueRouter({
  mode: "history",
  base: process.env.BASE_URL,
  routes,
});

export default router;
