<template>
  <div id="loginPage">
    <div class="login-container">
      <img src="../assets/logo.svg" >
      <v-alert width="100%" type="error" v-if="error">خطأ في اسم المستخدم او كلمة المرور</v-alert>
      <v-form ref="loginRef" v-model="valid" @submit.prevent="login">
        <v-text-field
          outlined
          v-model="username"
          label="اسم المستخدم"
          type="text"
          clearable
          :rules="inputRules"
        ></v-text-field>
        <v-text-field
          outlined
          v-model="password"
          label="كلمة المرور"
          :type="showPassword ? 'text' : 'password'"
          clearable
          :rules="inputRules"
          :append-icon="showPassword ? 'visibility_off' : 'visibility'"
          @click:append="showPassword = !showPassword"
        ></v-text-field>
        <v-btn block color="primary" type="submit" :loading="loading" large>
          تسجيل الدخول
        </v-btn>
      </v-form>
    </div>
  </div>
</template>

<script>
export default {
  name: "Login",
  data() {
    return {
      valid: false,
      loading: false,
      username: "",
      password: "",
      showPassword: false,
      error: false,
      inputRules: [(v) => !!v || "لا يمكن ترك الحقل فارغ!"],
    };
  },
  methods: {
    login() {
      this.loading = true;
      this.error = false;
      setTimeout(() => {
        if (this.$refs.loginRef.validate()) {
          this.$store
            .dispatch("loginAsAdmin", {
              username: this.username,
              password: this.password,
            })
            .then((result) => {
              this.axios.defaults.headers.common['Authorization'] = result;
              this.$router.push({
                path: '/'
              });
              this.loading = false;
            })
            .catch((err) => {
              this.error = true;
              this.loading = false;
            });
          
        } else {
          this.loading = false;
        }
      }, 1500);
    },
  },
};
</script>

<style lang="scss">

#loginPage {
  height: 100vh;
  width: 100vw;

  .login-container {
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    width: 650px;
    margin: 0 auto;

    @media(max-width: 767px) {
      width: 100%;
    }

    img {
      display: block;
      width: 200px;
      margin-bottom: 50px;
    }
    
    form {
      width: 100%;

      @media(max-width: 767px) {
        width: 85%;
      }
    }
  }
}
</style>