<template>
  <div id="productsPage" class="pa-4">
    <Loading :show="isLoading" />
    <v-card>
      <v-toolbar color="primary" flat>
        <span class="secondary--text">المنتجات</span>
        <v-spacer></v-spacer>
        <v-btn color="secondary" depressed @click="dialogs.create = true">
          <span class="primary--text">اضافة منتج جديد</span>
        </v-btn>
      </v-toolbar>
      <v-data-table :headers="headers" :items="products">
        <template v-slot:[`item.actions`]="{ item }">
          <v-btn icon @click="getProductInfo(item)">
            <v-icon>edit</v-icon>
          </v-btn>
          <v-btn icon @click="deleteProduct(item.id)">
            <v-icon>delete</v-icon>
          </v-btn>
        </template>

        <template v-slot:[`item.image`]="{ item }">
          <img
            :src="backend + '/attachments/' + item.image"
            width="50"
            height="50"
          />
        </template>
      </v-data-table>
    </v-card>

    <v-dialog v-model="dialogs.create" max-width="650px">
      <v-card>
        <v-toolbar flat>
          <span>اضافة منتج</span>
          <v-spacer></v-spacer>
          <v-btn
            icon
            @click="
              dialogs.create = false;
              $refs.createRef.reset();
            "
          >
            <v-icon>close</v-icon>
          </v-btn>
        </v-toolbar>
        <v-divider></v-divider>
        <v-form
          ref="createRef"
          v-model="forms.create.valid"
          lazy-validation
          class="pa-4"
          @submit.prevent="svaeProduct"
        >
          <v-text-field
            type="text"
            v-model="forms.create.name"
            label="اسم المنتج"
            outlined
            :rules="forms.create.rules"
          ></v-text-field>

          <v-text-field
            type="number"
            v-model="forms.create.price"
            label="سعرالمنتج"
            outlined
            :rules="forms.create.rules"
          ></v-text-field>

          <v-text-field
            type="number"
            v-model="forms.create.quantity"
            label="كمية المنتج"
            outlined
            :rules="forms.create.rules"
          ></v-text-field>

          <v-textarea
            v-model="forms.create.description"
            label="وصف المنتج"
            outlined
            :rules="forms.create.rules"
          ></v-textarea>

          <v-file-input
            label="صورة المنتج"
            outlined
            prepend-icon=""
            :rules="forms.create.rules"
            @change="getImage($event)"
          ></v-file-input>

          <v-btn block color="secondary" large type="submit">
            <span>اضافة منتج جديد</span>
          </v-btn>
        </v-form>
      </v-card>
    </v-dialog>

    <v-dialog v-model="dialogs.update" max-width="650px">
      <v-card>
        <v-toolbar flat>
          <span>تحديث منتج</span>
          <v-spacer></v-spacer>
          <v-btn
            icon
            @click="
              dialogs.update = false;
              $refs.updateRef.reset();
            "
          >
            <v-icon>close</v-icon>
          </v-btn>
        </v-toolbar>
        <v-divider></v-divider>
        <v-form
          ref="updateRef"
          v-model="forms.update.valid"
          lazy-validation
          class="pa-4"
          @submit.prevent="updateProduct(forms.update.product_id)"
        >
          <v-text-field
            type="text"
            v-model="forms.update.name"
            label="اسم المنتج"
            outlined
            :rules="forms.update.rules"
          ></v-text-field>

          <v-text-field
            type="number"
            v-model="forms.update.price"
            label="سعرالمنتج"
            outlined
            :rules="forms.update.rules"
          ></v-text-field>

          <v-text-field
            type="number"
            v-model="forms.update.quantity"
            label="كمية المنتج"
            outlined
            :rules="forms.update.rules"
          ></v-text-field>

          <v-textarea
            v-model="forms.update.description"
            label="وصف المنتج"
            outlined
            :rules="forms.update.rules"
          ></v-textarea>

          <v-file-input
            label="صورة المنتج"
            outlined
            prepend-icon=""
            @change="getImage($event)"
          ></v-file-input>

          <v-btn block color="secondary" large type="submit">
            <span>تعديل المنتج</span>
          </v-btn>
        </v-form>
      </v-card>
    </v-dialog>
  </div>
</template>

<script>
import Loading from "../components/Loading";
export default {
  components: {
    Loading,
  },
  data() {
    return {
      isLoading: false,
      dialogs: {
        create: false,
        update: false,
      },
      forms: {
        create: {
          valid: false,
          name: "",
          price: "",
          quantity: "",
          description: "",
          image: "",
          rules: [(v) => !!v || "الحقل مطلوب"],
        },
        update: {
          valid: false,
          name: "",
          price: "",
          quantity: "",
          description: "",
          product_id: "",
          image: "",
          rules: [(v) => !!v || "الحقل مطلوب"],
        },
      },
      products: [],
      headers: [
        {
          text: "#",
          value: "id",
          sortable: true,
          align: "start",
        },
        {
          text: "اسم المنتج",
          value: "name",
          sortable: false,
        },
        {
          text: "الوصف",
          value: "description",
          sortable: false,
        },
        {
          text: "السعر",
          value: "price",
          sortable: false,
        },
        {
          text: "الكمية",
          value: "quantity",
          sortable: false,
        },
        {
          text: "الصورة",
          value: "image",
          sortable: false,
        },
        {
          text: "اجراء",
          value: "actions",
          sortable: false,
        },
      ],
      image: "",
    };
  },

  mounted() {
    this.getProducts();
  },

  methods: {
    getProducts() {
      let self = this;
      self.isLoading = true;
      self.axios
        .get("/api/products/read")
        .then((result) => {
          self.products = result.data.reverse();
          self.isLoading = false;
        })
        .catch((error) => {
          console.log(JSON.stringify(error));
          self.isLoading = false;
        });
    },

    getImage(file) {
      let self = this;
      self.image = file;
    },

    svaeProduct() {
      let self = this;
      if (self.$refs.createRef.validate()) {
        self.isLoading = true;
        let createdProduct = {
          name: self.forms.create.name,
          price: self.forms.create.price,
          description: self.forms.create.description,
          quantity: self.forms.create.quantity,
        };
        let formData = new FormData();
        formData.append("image", self.image);

        self.axios
          .post("/api/products/create", createdProduct)
          .then((result) => {
            self.axios
              .put(`/api/products/upload/${result.data.id}`, formData)
              .then((data) => {
                self.getProducts();
                self.dialogs.create = false;
                self.isLoading = false;
              })
              .catch((err) => {
                console.error(JSON.stringify(err));
                self.isLoading = false;
              });
          })
          .catch((error) => {
            console.error(JSON.stringify(error));
            self.isLoading = false;
          });
      } else {
        self.isLoading = false;
      }
    },

    getProductInfo(item) {
      let self = this;
      self.dialogs.update = true;
      self.forms.update = {
        name: item.name,
        price: item.price,
        description: item.description,
        quantity: item.quantity,
        product_id: item.id
      };
    },

    updateProduct(id) {
      let self = this;
      let product = {
        name: self.forms.update.name,
        price: self.forms.update.price,
        description: self.forms.update.description,
        quantity: self.forms.update.quantity
      }
      self.isLoading = true;
      if (self.image === "") {
        self.axios
          .put(`/api/products/update/${id}`, product)
          .then((result) => {
            self.dialogs.update = false;
            self.$refs.updateRef.reset();
            self.getProducts();
            self.isLoading = false;
          })
          .catch((error) => {
            console.error(error);
            self.isLoading = false;
          });
      } else {
        let formData = new FormData();
        formData.append("image", self.image);
        self.axios
          .put(`/api/products/update/${id}`, product)
          .then((result) => {
            self.dialogs.update = false;
            self.$refs.updateRef.reset();
            self.getProducts();
            self.axios
              .put(`/api/products/upload/${result.data.id}`, formData)
              .then((data) => {
                self.getProducts();
                self.dialogs.create = false;
                self.isLoading = false;
              })
              .catch((err) => {
                console.error(JSON.stringify(err));
                self.isLoading = false;
              });
          })
          .catch((error) => {
            console.error(error);
            self.isLoading = false;
          });
      }
    },

    deleteProduct(id) {
      let self = this;
      let confirm = window.confirm("حذف المنتج؟");
      self.isLoading = true;
      if (confirm) {
        self.axios
          .delete(`/api/products/delete/${id}`)
          .then((result) => {
            self.getProducts();
            self.isLoading = false;
          })
          .catch((err) => {
            console.error(JSON.stringify(err));
            self.isLoading = false;
          });
      } else {
        self.isLoading = false;
      }
    },
  },
};
</script>

<style lang="scss">
</style>